<template>
  <div class="kanji-nav-container">
    <div v-for="kanji in kanjiNav" :key="kanji.position">
      <span 
        :class="['kanji-link', {'current-kanji': (kanji.position === currentIndex)}]" 
        @click="linkClicked(kanji.position)">
        {{kanji.character}}
      </span>
    </div>
  </div>
</template>

<script>
/*
  global
    window
*/

export default {
  name: "KanjiNav",
  props: {
    currentIndex: Number,
  },
  data: ()=>({}),
  methods: {
    linkClicked(position) {
      this.$emit('linkClicked', position)
    }
  },
  computed: {
    kanjiNav() {
      let start = this.currentIndex - 10;
      start = start < 0 ? 0 : start;
      return [...Array(21)].map((_, i) => {
        return {
          character: window.KANJI_MAP[start + i],
          position: start + i
        };
      });
    },
  } 
}
</script>

<style lang="scss" scoped>
  .kanji-nav-container {
    position: absolute;
    bottom: 15%;
    left: 50%;
    transform: translateX(-50%);
    
    width: 50%;
    display: flex;
    > div {
      width: 50%;
    }
    .kanji-link {
      cursor: pointer;
      user-select: none;
    }
  }

  ////*MOBILE*/
  @media (max-width: 1450px) { 
    .kanji-nav-container {
      top: 0px;
      bottom: unset;
      width: calc(90% - 27px);
      margin: 0 auto;
      overflow-x: auto;
      padding: 20px;
      padding-top: 15px;
      > div {
        margin-right: 20px;
      }
      scroll-behavior: smooth;
    }
  }

   @media (max-width: 700px) { 
      width: calc(100% - 27px);
   }
</style>