<template>
<div class="screen-mask" v-if="!hidden">
  <div v-bind:class="[{'banner-active': active}, 'cookie-banner']" >
    <div class="cookie-info-container">
      <h4>Cookie Consent</h4>
      <p>This app uses a cookie to store the last kanji you saw (that's it), but before we can store that we need your permission</p>
    </div>
    <div class="cookie-button-container">
      <button id="accept-cookie" @click="acceptCookies">Sounds good</button>
      <button id="reject-cookie" @click="hide">Keep your cookie to yourself</button>
    </div>
  </div>
  </div>
</template>
<script>
export default {
  name: "CookieBanner",
  data: () => {
    return {
      hidden: true,
      active: false,
    };
  },
  props:['hasCookie'],
  methods: {
    acceptCookies(){
      this.$emit('cookieAccepted');
      this.hide()
    },
    hide(){
      this.hidden = true
    }
  },
  mounted(){
    this.hidden = this.hasCookie
    let $this = this;
    setTimeout(()=>{ $this.active = !$this.hidden }, 500)
  },
}
</script>

<style scoped lang="scss">
.screen-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  background: transparent;
}
.cookie-banner {
  
  position: absolute;
  text-align: left;
  max-width: 800px;
  width: 80vw;
  border:solid 1px black;
  background: whitesmoke;
  z-index: 1001;
  padding: 2rem;
  font-size: 20px;
  top: -100%;
  left: 50%;
  transform: translateX(-50%);
  user-select: none;

  h4 {
    margin-top: 0;
  }
  .cookie-button-container {
    button {
      color: white;
      border: 2px solid #555;
      appearance: none;
      padding: 0.65rem;
      border-radius: 12px;
      margin-right: 25px;
      font-size: 20px;
      margin-bottom: 10px;
    }

    #accept-cookie {
      background: #337ccc;

    }
    #reject-cookie {
      background: #dd2222;
    }
  }
}
.banner-active {
  top: 5px;
  transition: top cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
}
</style>
