<template>
    <div
      class="go-back"
      v-if="anchorActive"
      @click="goBackToAnchor()"
      :title="`Return to ${anchoredKanji.kanji}`"
    >
      <span>
        <svg viewBox="0 0 32 32" class="icon icon-caret-left" aria-hidden="true">
          <path d="M20.697 24L9.303 16.003 20.697 8z"/>
        </svg>
        {{anchoredKanji.kanji}}
      </span>
    </div>
</template>
<script>
/*
  global
    window
*/

export default {
  name: "KanjiPositionAnchor",
  data: () => {
    return {
      anchoredKanji: {
        kanji: false,
        index: 0
      },
      strokeImg: null,
      kanjiInfo: null,
    };
  },
  methods: {
    goBackToAnchor() {
      const returnIndex = this.anchoredKanji.index;
      this.anchoredKanji.kanji = false;
      this.$emit('returnToAnchor', returnIndex);
    },
    setAnchor(index) {
      if (!this.anchorActive) {
        this.anchoredKanji.index = index;
        this.anchoredKanji.kanji = window.KANJI_MAP[index];
      }
    },
  },
  computed: {
    anchorActive() {
      return !!this.anchoredKanji.kanji
    }
  },
}
</script>
<style scoped lang="scss">
.go-back {
  position: absolute;
  top: 80px;
  left: 80px;

  span {
    font-size: 1.5em;
    cursor: pointer;
    user-select: none;
  }
}

.icon-caret-left {
  position: relative;
  width: 1.25em;
  height: 1.25em;
  top: 0.25em;
}

////*MOBILE*/
@media (max-width: 1450px) { 
  .go-back {
    top: 75px;
    left: 10px;
    z-index: 10;
  }
}
</style>