<template>
  <li v-if="list">
    <div class="info-heading">
      {{heading}}
    </div>
    <div class="kana-info">
      <div v-for="(kana, index) in list.split(',')"
        :key="kana + index">
      <ruby v-for="(char, index) in kana.split('')" 
        :key="char+index">
        <template v-if="char !== ' '">
          {{/* Romaji super-scripted on kana */}}
          {{char}}<rp>(</rp><rt>{{char !== '-' ? toRomaji(char) : ' '}}</rt><rp>)</rp>
        </template>
      </ruby>
      </div>
    </div>
  </li>
</template>
<script>
import { toRomaji } from "wanakana";
export default {
  name: "PronunciationList",
  props: {
    list: String, 
    heading: String
  },
  data: () => ({
    toRomaji: toRomaji
  }),
}
</script>
<style lang="scss" scoped>
  .kana-info {
    display: flex;
    overflow-x: auto;
    text-align: right;
    padding-bottom: 10px;
    > div {
      display: inline-block;
      flex: none;
      margin-left: 30px;
      text-align: center;
      border-bottom: solid 2px lightgray;

      &:first-child {
        margin-left: auto;
      }
    }
  }

  ////*MOBILE*/
  @media (max-width: 1450px) { 
      .kana-info {
      text-align: center;
      justify-content: center;
      flex-wrap: wrap;
      > div {
        flex: unset;
        margin: 5px 10px;

        &:first-child {
          margin-left: 10px;
        }
      }
    }
  }
</style>