<template >
  <div class="main" v-if="kanjiInfo">
    <!-- <CookieBanner :hasCookie="userHasCookie" @cookieAccepted="setCookie()"/> -->
    <KanjiPositionAnchor
      @returnToAnchor="setKanjiIndex"
      ref="kanjiAnchor"/>
    <div class="center-row">
      <div class="info-container">
        <div class="info-list">
          <ul>
            <PronunciationList 
              :list="kanjiInfo.info.kun"
              heading="Kunyomi"/>
            <PronunciationList 
              :list="kanjiInfo.info.on"
              heading="Onyomi"/>
            <TranslationList 
              :meanings="kanjiInfo.info.meaning"/>
            <StrokeOrderDiagram 
              :kanji="kanji"/>
          </ul>
        </div>
      </div>
      <KanjiShowcase :kanji="kanji"/>
      <div class="right-side-container">
        <UsageList 
        :examples="kanjiInfo.info.examples" 
        @lookupRequested="kanjiLookup"/>
      </div>
    </div>
    <KanjiNav
      @linkClicked="setKanjiIndex"
      :currentIndex="userKanjiIndex"/>
    <JishoLookupButton 
      :kanji="kanji"/>
  </div>
</template>

<script>
/*
  global
    window
*/
import Cookies from "js-cookie";
import { toRomaji } from "wanakana";
import {
  CookieBanner,
  KanjiPositionAnchor,
  PronunciationList,
  TranslationList,
  StrokeOrderDiagram,
  KanjiShowcase,
  UsageList,
  KanjiNav,
  JishoLookupButton,
  } from '../components';

const KANJI_MAP = window.KANJI_MAP = require("../kanji_map.json");

export default {
  name: "Main",
  data: () => ({
    userKanjiIndex: 0,
    strokeImg: null,
    kanjiInfo: null,   
  }),
  components: {
    CookieBanner, 
    KanjiPositionAnchor, 
    PronunciationList,
    TranslationList,
    StrokeOrderDiagram,
    KanjiShowcase,
    UsageList,
    KanjiNav,
    JishoLookupButton,
  },
  watch: {
    userKanjiIndex: function (kanjiIndex) {
      this.updateKanjiInfo()
    }
  },
  methods: {
    getKanjiIndexFromCookie() {
      const c = Cookies.get("kanjiIndex");
      if (c) {
        this.userKanjiIndex = parseInt(c);
        this.setKanjiIndex(
          this.userKanjiIndex + 1
        );
      } else {
        this.userKanjiIndex = 0;
        if(!this.userHasCookie) return;
        this.setCookie()
      }
    },
    setKanjiIndex(newIndex) {
      this.userKanjiIndex = newIndex;
      if(!this.userHasCookie) return;
      this.setCookie()
    },
    setCookie(){
      Cookies.set(
        "kanjiIndex", 
        this.userKanjiIndex, 
        { expires: 365, sameSite: 'strict'}
      );
    },
    updateKanjiInfo() {
      import('../assets/character_data/'+this.kanji+'.json')
        .then(kanjiModule => 
          (this.kanjiInfo = kanjiModule.default) 
        ).catch(console.log)
    },
    kanjiLookup(lookUpVal) {
      const newIndex = KANJI_MAP.indexOf(lookUpVal);
      if (newIndex != -1 && this.userKanjiIndex !== newIndex) {
        this.$refs.kanjiAnchor.setAnchor(this.userKanjiIndex);
        this.userKanjiIndex = newIndex;
        document.body.scrollTop = document.documentElement.scrollTop = 0;
      }
    },
    bindPasteLookupEvent() {
      const $this = this;
      window.addEventListener('paste', (e) => 
        $this.kanjiLookup(
          e.clipboardData.getData('text')
        )
      )
    },
    initialLoad() {
      this.getKanjiIndexFromCookie();
      this.updateKanjiInfo();
      this.setCookie();
      this.bindPasteLookupEvent();
    },
  },
  computed: {
    kanji() {
      let kanjiAtMapIndex = KANJI_MAP[this.userKanjiIndex];
      
      // Reset to beginning of the list if the index goes out of bounds
      if (!kanjiAtMapIndex) {
        this.userKanjiIndex = 0
        kanjiAtMapIndex = KANJI_MAP[0];
      }
      return kanjiAtMapIndex;
    },
    userHasCookie() {
      return Cookies.get("kanjiIndex") !== undefined;
    },
  },
  created() {
    this.initialLoad();
  },
};
</script>

