<template>
  <li>
    <div class="stroke-order-container">
      <div class="info-heading">Stroke Order</div>
      <img :src="strokeImg" :alt="`Stoke order for ${kanji}`" />
    </div>
  </li>
</template>

<script>
  export default {
    name: "StrokeOrderDiagram",
    props: {
      kanji: String,
      
    },
    data: ()=>({
      strokeImg: null,
    }),
    watch: {
      kanji: function() {
        this.updateStrokeImg()
      }
    },
    methods: {
      updateStrokeImg() {
        import('../assets/strokes/' + this.kanji + '.png')
          .then(r => 
            (this.strokeImg = r.default)
          )
          .catch(console.log)
      },
    },
    mounted() {
      this.updateStrokeImg();
    }
  }
</script>

<style lang="scss" scoped>
  .info-heading {
    margin-bottom: 20px;
  }
</style>