<template>
  <div class="usage-container">
  <div class="info-heading">Usage</div>
  <ul class="usage-list">
    <li v-for="(example, index) in examples" :key="index">
      <div class="example-container">
        <div class="word-reading-container">
          <div class="example-word">
            <span
              v-for="(character, index) in example.word"
              :key="character+index"
              @click="requestLookup(character)"
            >{{character}}</span>
          </div>
          <div class="example-reading">({{example.reading}})</div>
        </div>
        <div class="example-definition">{{example.meaning}}</div>
      </div>
    </li>
  </ul>
</div>
</template>

<script>
export default {
  name: "UsageList",
  props: {
    examples: Array
  },
  data: ()=>({}),
  methods: {
    requestLookup(character){
      this.$emit('lookupRequested', character)
    }
  }
}
</script>

<style lang="scss" scoped>
.usage-container {
  display: block;
  height: 100%;
}
.usage-list {
  height: 100%;
  overflow-y: auto;
  font-size: 0.8em;
}

.example-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  flex-wrap: nowrap;
  align-items: baseline;
  text-align: left;

  .word-reading-container {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    flex-grow: 1;
  }
  .example-word {
    font-size: 1.5em;
    span {
      user-select: none;
      cursor: pointer;
    }
  }
  .example-reading {
    flex-grow: 1;
  }
  .example-definition {
    flex-shrink: 1;
  }
}

////*MOBILE*/
@media (max-width: 1450px) { 
  .example-container {
  display: block;
  text-align: center;

    .example-definition {
      font-style: italic;
    }
  }
  .usage-list {
    padding: 0 15px;
  }
}
</style>