<template>
  <div
    class="kanji-showcase"
    @click="copyText(kanji)"
    :title="`Click to copy ${kanji} to your clipboard`"
  >
    <h1>{{kanji}}</h1>
  </div>
</template>

<script>
/*
  global
    navigator
*/
export default {
  name: "KanjiShowcase",
  props: {
    kanji: String
  },
  data: ()=>({}),
  methods: {
    copyText(text) {
      navigator.clipboard.writeText(text);
    },
  }
}
</script>

<style lang="scss" scoped>
  .kanji-showcase {
    margin: 0 40px;
    flex-grow: 1;
    width: 50%;
    user-select: none;
    h1 {
      font-size: 20em;
      margin: 0;
    }
  }

  ////*MOBILE*/
  @media (max-width: 1450px) { 
    .kanji-showcase {
      grid-area: kanji;
      width: 100%;
      margin: 80px auto 0 auto;
      h1 {
        font-size: 10em;
      }
    }
  }
</style>